.sidebar_chats::-webkit-scrollbar {
  display: none;
}

.sidebar {
  display: flex;
  flex-direction: column;
  flex: 0.3;
}
.sidebar_header {
  display: flex;
  justify-content: space-between;
  padding: 20px;
  border-right: 1px solid lightgray;
}
.sidebar_headerRight {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-width: 10vw;
}
.sidebar_headerRight > .MuiSvgIcon-root {
  margin-right: 2vw;
  font-size: 24px !important;
}

.sidebar_search {
  display: flex;
  align-items: center;
  background-color: #f6f6f6;
  height: 39px;
  padding: 10px;
}
.sidebar_searchContainer {
  display: flex;
  align-items: center;
  background-color: white;
  width: 100%;
  height: 35px;
  border-radius: 20px;
}

.sidebar_searchContainer .MuiSvgIcon-root {
  color: gray;
  padding: 10px;
}
.sidebar_searchContainer > input {
  border: none;
  outline-width: 0;
  margin-left: 10px;
}
.sidebar_chats {
  flex: 1;
  background-color: white;
  overflow: scroll;
}
.vertIcon {
  position: relative;
}
.dropmenu_one {
  /* display: float; */
  background-color: white;
  text-align: left;
  font-size: 14.5px;
  font-weight: 400;
  padding: 5px 0;
  width: 150px;
  line-height: 40px;
  box-shadow: 0px 0px 3px 4px lightgray;
  position: absolute;
  top: 99%;
  left: -7rem;
  border-radius: 2px;
  z-index: 2;
  animation: 3s ease-in 1s infinite reverse both running slidein;
}

.dropmenu_one ul {
  list-style: none;
  margin: 0 !important;
  padding: 0 !important;
}

.dropmenu_one ul li {
  height: 40px;
  padding-left: 15px;
  cursor: pointer;
}

.dropmenu_one ul li:hover {
  background-color: rgb(243, 240, 240);
}
